class e{static parse(r){if(r.length>1)throw new Error('"id" does not expect arguments');return new e}evaluate(e,r){return e.id}}class r{static parse(e){if(e.length>1)throw new Error('"geometry-type" does not expect arguments');return new r}evaluate(e,r){switch(e.type){case 0:return"Unknown";case 1:return"Point";case 2:return"LineString";case 3:return"Polygon"}}}class t{static parse(e){if(e.length>1)throw new Error('"properties" does not expect arguments');return new t}evaluate(e,r){return e.values}}class n{static parse(e){if(e.length>1)throw new Error('"zoom" does not expect arguments');return new n}evaluate(e,r){return r}}export{r as GeomType,e as ID,t as Properties,n as Zoom};
